import React from 'react';


const hideRestaurant = (menuList, setMenuList, restaurantName) => {
    setMenuList(menuList.filter(item => item["RestaurantName"] !== restaurantName))
}



const Content = ({Name, Price, Components}) => { //Renderöi nimen (esim Lounas), hinnan ja sit mitä siihe kuuluu
    return (
        <div className="content">
            <div className="subheader">
                <h4>{Name}</h4>
                <span><b>{Price} €</b></span>
            </div>
            {Components.map((component) =>
            <p key={component}>{component}</p>
            )}
        </div>
    )
}




const Restaurant = ({RestaurantName, MenusForDays, menuList, setMenuList}) => { //Renderöi ravintolan "laatikon" ja ravintolan nimen

    return MenusForDays[0]["SetMenus"].length === 0 ? <div>
    </div>
        :
        (
        <div className="menu-container" onClick={() => hideRestaurant(menuList, setMenuList, RestaurantName)}>
            <div className="restaurant-name">
                <h1>{RestaurantName}</h1>
            </div>

            {MenusForDays[0]["SetMenus"].map(menu =>
            <Content key={menu.Name}{...menu}/>)}
        </div>
    )
}
export default Restaurant;

